import React, {Component, lazy, Suspense} from 'react';
import {
	Badge,
	Card,
	CardBody,
	CardFooter,
	CardHeader,
	CardTitle,
	List,
	ListGroup,
	ListGroupItem,
	ListGroupItemHeading,
	ListGroupItemText,
	Collapse,
	Form,
	FormGroup,
	Label,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	Input,
	Fade,
	Col,
	Row,
	Button
} from 'reactstrap';
import {CustomTooltips} from '@coreui/coreui-plugin-chartjs-custom-tooltips';
import {getStyle, hexToRgba} from '@coreui/coreui/dist/js/coreui-utilities'
import axios from 'axios';

class UserWishList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			userID: sessionStorage.getItem("user_id"),
			modal: false,
			success: false,
			warning: false,
			info: false,
			successAddWishList: false,
			successDeleteRestaurant: false,
			successDeleteWishList: false,
			wishlist: [
				{
					wid: "1",
					name: "Default List",
					create_time: "2018\/10\/9 19:04:11",
					restaurants: [{
						rid: 1,
						name: "Metro Pizza",
					},
						{
							rid: 2,
							name: "Buddy V's Ristorante E Caffe"
						}
					]
				},
				{
					wid: "2",
					name: "My Favorite Pizza Restaurants",
					create_time: "2018\/10\/9 19:04:11",
					restaurants: [{
						rid: 1,
						name: "Metro Pizza"
					},
						{
							rid: 2,
							name: "Buddy V's Ristorante E Caffe"
						}
					]
				},
				{
					wid: "3",
					name: "Nearby Chinese Restaurants",
					create_time: "2018\/10\/9 19:04:11",
					restaurants: [{
						rid: 1,
						name: "Metro Pizza"
					},
						{
							rid: 2,
							name: "Buddy V's Ristorante E Caffe"
						}
					]
				},
			],
			collapse: false,
			accordion: [true, false, false],
			custom: [true, false],
			status: 'Closed',
			fadeIn: true,
			timeout: 300
		};
		this.toggleInfo = this.toggleInfo.bind(this);
		this.toggleAccordion = this.toggleAccordion.bind(this);
		this.addWishList = this.addWishList.bind(this);
		this.toggle = this.toggle.bind(this);
		this.toggleSuccess = this.toggleSuccess.bind(this);
		this.toggleWarning = this.toggleWarning.bind(this);
		this.toggleSuccessAddWishList = this.toggleSuccessAddWishList.bind(this);
		this.toggleDeleteRestaurantSuccess = this.toggleDeleteRestaurantSuccess.bind(this);
		this.toggleSuccessDeleteWishList = this.toggleSuccessDeleteWishList.bind(this);
		this.deleteRestaurant = this.deleteRestaurant.bind(this);
		this.deleteWishList = this.deleteWishList.bind(this);
		this.updateWishList = this.updateWishList.bind(this);
		this.setUpAccording = this.setUpAccording.bind(this);
		this.updateWishList();
	}

	setUpAccording(length) {
		let array = [];
		array.push(true);
		for (let i = 1; i < length; i++) {
			array.push(false);
		}
		this.setState({
			accordion: array
		});
	}

	updateWishList() {
		axios.get('http://34.83.23.47:8080/api/wishlist/' + this.state.userID)
			.then(response => {
				this.setState({
					wishlist: response.data,
				});
				this.setUpAccording(this.state.wishlist.length);
			}, error => {
				console.log(error);
			});
	}

	deleteRestaurant(rid, wid) {
		if (this.state.userID == undefined) {
			window.alert("You have not login yet, please login first!");
		} else {
			axios.delete('http://34.83.23.47:8080/api/wishlist/' + this.state.userID, {
				data: {
					wid: wid,
					rid: rid
				}
			})
				.then(response => {
						this.toggleDeleteRestaurantSuccess();
						this.updateWishList();
					},
					error => {
						console.log(error);
					});
		}
	}

	deleteWishList(wid) {
		if (this.state.userID == undefined) {
			window.alert("You have not login yet, please login first!");
		} else {
			axios.delete('http://34.83.23.47:8080/api/wishlist', {
				data: {
					wid: wid,
					uid: this.state.userID
				}
			})
				.then(response => {
						this.toggleSuccessDeleteWishList();
						this.updateWishList();
					},
					error => {
						console.log(error);
					});
		}
	}

	toggle() {
		if (this.state.userID == undefined) {
			window.alert("You have not login yet, please login first!");
		} else {
			this.setState({
				modal: !this.state.modal,
			});
		}
	}

	toggleSuccess() {
		this.setState({
			success: !this.state.success,
		});
	}

	toggleWarning() {
		this.setState({
			warning: !this.state.warning,
		});
	}

	toggleInfo() {
		this.setState({
			info: !this.state.info,
		});
	}

	toggleSuccessAddWishList() {
		this.setState({
			successAddWishList: !this.state.successAddWishList,
		});
	}

	toggleSuccessDeleteWishList() {
		this.setState({
			successDeleteWishList: !this.state.successDeleteWishList,
		});
	}

	toggleDeleteRestaurantSuccess() {
		this.setState({
			successDeleteRestaurant: !this.state.successDeleteRestaurant,
		});
	}

	addWishList() {
		this.toggle();
		var newWishlistName = document.getElementById("newWishListTextArea").value;
		//http request update
		axios.post('http://34.83.23.47:8080/api/wishlist', {
			uid: this.state.userID,
			name: newWishlistName
		})
			.then(response => {
					this.toggleSuccess();
					this.updateWishList();
				},
				error => {
					console.log(error);
				});
	}

	toggleAccordion(tab) {
		const prevState = this.state.accordion;
		const state = prevState.map((x, index) => tab === index ? !x : false);

		this.setState({
			accordion: state,
		});
	}

	render() {

		return (
			<div className="animated fadeIn normal-wrapper">
				<div className="wishlist-wrapper">
					<h1>Wishlist</h1>
					<span onClick={() => this.toggle()} className="create-wishlist bg-orange text-white">
						<i className="fa fa-plus fa-adjust-aligh"/>
						<span> Create Wishlist</span>
					</span>
				</div>
				<div id="accordion">
					<Row>
						{this.state.wishlist.map((object, i) => {
							return <Col lg="6" key={i}><Card>
								<CardHeader className="bg-primary" id={"heading" + i}>
									<Button block color="link" className="text-left m-0 p-0"
											onClick={() => this.toggleAccordion(i)}
											aria-expanded={this.state.accordion[0]}
											aria-controls="collapseOne">
										<h4 className="d-inline-block m-0 p-0 text-white">Wishlist {i + 1}: {object.name}</h4>
									</Button>
									<Button
										className="btn-small btn-pill float-right btn-outline-light"
										onClick={this.deleteWishList.bind(this, object.wid)}>Delete</Button>
								</CardHeader>
								<Collapse isOpen={this.state.accordion[i]} data-parent="#accordion"
										  id={"collapse" + i} aria-labelledby={"heading" + i}>
									<CardBody>
										<ListGroup>
											{object.restaurants.map((res, index) => {
													return <ListGroupItem key={index}
																		  className="border-left-0 border-right-0">
														<h5 className="d-inline-block">{res.name}</h5>
														<Button
															className="d-inline-block btn-small btn-pill float-right btn-outline-danger"
															onClick={this.deleteRestaurant.bind(this, res.rid, object.wid)}>Delete</Button>
													</ListGroupItem>
												}
											)}
										</ListGroup>
									</CardBody>
								</Collapse>
							</Card>
							</Col>
						})}
					</Row>
				</div>
				<Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
					<ModalHeader toggle={this.toggle}>New Wishlist</ModalHeader>
					<ModalBody>
						<FormGroup row>
							<Col xs="12" md="12">
								<Label>Please enter name for new wishlist</Label>
								<Input type="textField" rows="9" id="newWishListTextArea" placeholder="Name..."/>
							</Col>
						</FormGroup>
					</ModalBody>
					<ModalFooter>
						<Button color="primary" onClick={this.addWishList}>Submit</Button>{' '}
						<Button color="secondary" onClick={this.toggle}>Cancel</Button>
					</ModalFooter>
				</Modal>
				<Modal isOpen={this.state.success} toggle={this.toggleSuccess}
					   className={'modal-success ' + this.props.className}>
					<ModalHeader toggle={this.toggleSuccess}>Add Wishlist Success</ModalHeader>
					<ModalBody>
						A new wishlist has been created!
					</ModalBody>
					<ModalFooter>
						<Button color="success" onClick={this.toggleSuccess}>OK</Button>{' '}
					</ModalFooter>
				</Modal>
				<Modal isOpen={this.state.successDeleteRestaurant} toggle={this.toggleDeleteRestaurantSuccess}
					   className={'modal-success ' + this.props.className}>
					<ModalHeader toggle={this.toggleDeleteRestaurantSuccess}>Delete Restaurant Success</ModalHeader>
					<ModalBody>
						Restaurant has been deleted from your wishlist!
					</ModalBody>
					<ModalFooter>
						<Button color="success" onClick={this.toggleDeleteRestaurantSuccess}>OK</Button>{' '}
					</ModalFooter>
				</Modal>
				<Modal isOpen={this.state.successDeleteWishList} toggle={this.toggleSuccessDeleteWishList}
					   className={'modal-success ' + this.props.className}>
					<ModalHeader toggle={this.toggleSuccessDeleteWishList}>Delete Wishlist Success</ModalHeader>
					<ModalBody>
						Wishlist has been deleted!
					</ModalBody>
					<ModalFooter>
						<Button color="success" onClick={this.toggleSuccessDeleteWishList}>OK</Button>{' '}
					</ModalFooter>
				</Modal>
			</div>
		);
	}
}

export default UserWishList;
